.Series{
  box-sizing: border-box;

  &__List {
    list-style: none;
    padding: 0;
  }
  
  &__Item {
  }

  &__button {
    width: 50px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: transparent;
    
    &:first-child {
      width: fit-content;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
